import React from "react";
import foto1 from "../Archivos/Imagenes/imprimiendo3d.gif"
import foto2 from "../Archivos/Imagenes/flex1.jpg"
import foto3 from "../Archivos/Imagenes/pla1.jpg"
import foto4 from "../Archivos/Imagenes/abs.webp"
import foto5 from "../Archivos/Imagenes/petg.jpg"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
function Nosotros() {
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);
  return (
    <div id="nosotros" data-aos="fade-left" className='Nosotres'>
      <div className='container'>
        <div className='container'>
          <h2 className='text-center py-3'>Acerca de Nosotros</h2>
        </div>
        <div>
          <p className='shadow-sm rounded border-dark p-2'>
          Somos un grupo de 3 amigos de toda la vida,
            apasionados por la impresin 3D. Hace ms de 4 aos que decidimos
            emprender por este camino que comenz como un hobby, pero con una
            pandemia de por medio y una gran demanda de productos hechos en 3D
            nos dimos cuenta que era ms que eso, sino que era un trabajo ms,
            algo que necesitaba que le dediquemos mucho ms tiempo y dedicacin,
            en ese momento nuestros objetivos cambiaron ya que pasamos de
            imprimir un par de cosas por semana a tener ms de 800 pedidos en
            cuestin de das. Nuestra principal finalidad es facilitarte el
            proceso de llevar tu idea o proyecto a la realidad y acompaarte en
            todo el proceso de impresin.

          </p>
        </div>
        <div>
          <div className='row'>
            <div className='col'>
              <h4>Que es una impresora 3D?</h4>
              <p className='shadow-sm rounded border-dark p-2'>
                Una impresora 3D es una mquina capaz de realizar rplicas de
                diseos en 3D, creando piezas o maquetas volumtricas a partir
                de un diseo hecho por ordenador. Surgen con la idea de
                convertir archivos de 2D en prototipos reales o 3D. Comnmente,
                se ha utilizado en el prefabricado de piezas o componentes, en
                sectores como la arquitectura y el diseo industrial. En la
                actualidad, se est extendiendo su uso en la fabricacin de todo
                tipo de objetos, modelos para vaciado, piezas complicadas,
                alimentos, prtesis mdicas (ya que la impresin 3D permite
                adaptar cada pieza fabricada a las caractersticas exactas de
                cada paciente).
              </p>
            </div>
            <div className='col'>
              <img src={foto1} className='w-50'></img>
              <p>
                Aca podemos ver como una impresora con tecnologia fdm
                imprimiendo.
              </p>
            </div>
          </div>

          <h4>Cuales son los materiales mas utilizados?</h4>

          <div className='container'>
            <div className='row'>
              <div className='col'>
                <img src={foto2} className='w-50'></img>

                <h4>PLA</h4>
                <p className='shadow-sm rounded border-dark p-2 '>
                  El pla es uno de los filamentos ms utilizados gracias a que
                  es muy estable y no necesita cama caliente. Se obtiene a
                  partir de materia como el maz y el trigo, hecho que le hace
                  no producir gases txicos al contrario que otros filamentos.
                  Adems de ser un material reciclable, ofrece mayor velocidad
                  de impresin que otras opciones en el mercado, siendo la
                  opcin preferida para aquellos usuarios que dan sus primeros
                  pasos en impresin 3D y tambin de usuarios experimentados.
                </p>
              </div>
              <div className='col'>
                <h4>FLEX</h4>
                <p className='shadow-sm rounded border-dark p-2 '>
                  El flex combina plstico con caucho y se caracteriza por su
                  posibilidad de estiramiento y su resistencia a los impactos y
                  a las roturas por maleabilidad. Adems, es un filamento
                  reciclable y su superficie presenta gran suavidad. Es
                  conveniente no fundirlo a temperaturas superiores a las
                  recomendadas para evitar la prdida de elasticidad. Este
                  material es muy habitual encontrarlo en piezas protectoras,
                  juguetes con flexibilidad, piezas decorativas, pulseras etc.
                </p>
                <img src={foto3} className='w-50'></img>
              </div>
            </div>
            <div className='row'>
              <div className='col'>
                <img src={foto4} className='w-50'></img>
                <h4>PETG</h4>
                <p className='shadow-sm rounded border-dark p-2 '>
                  El petg es un plstico muy similar al PET utilizado en envases
                  para alimentos y bebidas. Es un material muy resistente a la
                  corrosin, la temperatura, impactos y agentes qumicos. Adems
                  es impermeable y presenta una reducida absorcin de la
                  humedad, lo que lo convierte en ideal para conservar productos
                  de alimentacin.
                </p>
              </div>

              <div class='col'>
                <h4>ABS</h4>
                <p className='shadow-sm rounded border-dark p-2 '>
                  El abs es utilizado sobre todo en ambientes profesionales. Se
                  caracteriza por su gran resistencia, tanto a impactos como a
                  altas temperaturas. Adems, permite realizar trabajos sobre la
                  pieza una vez impresa. Tiene como desventaja que produce gases
                  nocivos en el momento de la impresin, lo que dificulta la
                  presencia de grupos de trabajo en torno a la impresora.
                </p>
                <img src={foto5} class='w-50 p-3 mb-3'></img>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Nosotros;
